const Privacy = `<p>
	<br />
</p>
<h1 style="text-align:center;font-family:var(--typeHeaderPrimary),var(--typeHeaderFallback);font-weight:var(--typeHeaderWeight);font-size:var(--typeHeaderSize);">
	<strong><span style="font-size:24px;">Privacy policy</span></strong>
</h1>
<p>
	<br />
</p>
<div class="shopify-policy__body" style="color:#132B50;font-family:&quot;font-size:16px;background-color:#FFFFFF;">
	<p>
		<br />
	</p>
	<p>
		<span>This Privacy Policy describes how your personal information is collected, used, and shared when you visit or make a purchase from frissonlife.com (the “Site”).&nbsp;</span>
	</p>
<br />
<span style="font-weight:700;font-size:18px;">Personal information we collect</span><span style="font-size:18px;">&nbsp;</span><br />
<span>When you visit the Site, we automatically collect certain information about your device, including information about your web browser, IP address, time zone, and some of the cookies that are installed on your device. Additionally, as you browse the Site, we collect information about the individual web pages or products that you view, what websites or search terms referred you to the Site, and information about how you interact with the Site. We refer to this automatically-collected information as “Device Information”.&nbsp;</span><br />
<br />
<span>We collect Device Information using the following technologies:&nbsp;</span><br />
<span>- “Cookies” are data files that are placed on your device or computer and often include an anonymous unique identifier. For more information about cookies, and how to disable cookies, visit http://www.allaboutcookies.org.&nbsp;</span><br />
<span>- “Log files” track actions occurring on the Site, and collect data including your IP address, browser type, Internet service provider, referring/exit pages, and date/time stamps.&nbsp;</span><br />
<span>- “Web beacons”, “tags”, and “pixels” are electronic files used to record information about how you browse the Site.&nbsp;</span><br />
<br />
<span>Additionally when you make a purchase or attempt to make a purchase through the Site, we collect certain information from you, including your name, billing address, shipping address, payment information (including credit card numbers), email address, and phone number. We refer to this information as “Order Information”.&nbsp;</span><br />
<br />
<span>When we talk about “Personal Information” in this Privacy Policy, we are talking both about Device Information and Order Information.&nbsp;</span><br />
<br />
<span style="font-weight:700;font-size:18px;">How do we use your personal information?</span><span style="font-size:18px;">&nbsp;</span><br />
<span>We use the Order Information that we collect generally to fulfill any orders placed through the Site (including processing your payment information, arranging for shipping, and providing you with invoices and/or order confirmations). Additionally, we use this Order Information to:&nbsp;</span><br />
<span>- Communicate with you;&nbsp;</span><br />
<span>- Screen our orders for potential risk or fraud; and&nbsp;</span><br />
<span>- When in line with the preferences you have shared with us, provide you with information or advertising relating to our products or services.&nbsp;</span><br />
<br />
<span>We use the Device Information that we collect to help us screen for potential risk and fraud (in particular, your IP address), and more generally to improve and optimize our Site (for example, by generating analytics about how our customers browse and interact with the Site, and to assess the success of our marketing and advertising campaigns).&nbsp;</span><br />
<br />
<span style="font-weight:700;font-size:18px;">Sharing you personal Information</span><span style="font-size:18px;">&nbsp;</span><br />
<span>We share your Personal Information with third parties to help us use your Personal Information, as described above. For example, we use Shopify to power our online store--you can read more about how Shopify uses your Personal Information here: https://www.shopify.com/legal/privacy. We also use Google Analytics to help us understand how our customers use the Site -- you can read more about how Google uses your Personal Information here: https://www.google.com/intl/en/policies/privacy/. You can also opt-out of Google Analytics here: https://tools.google.com/dlpage/gaoptout.&nbsp;</span><br />
<br />
<span>Finally, we may also share your Personal Information to comply with applicable laws and regulations, to respond to a subpoena, search warrant or other lawful request for information we receive, or to otherwise protect our rights.&nbsp;</span><br />
<br />
<span style="font-weight:700;font-size:18px;">Behavioural advertising</span><span style="font-size:18px;">&nbsp;</span><br />
<span>As described above, we use your Personal Information to provide you with targeted advertisements or marketing communications we believe may be of interest to you. For more information about how targeted advertising works, you can visit the Network Advertising Initiative’s (“NAI”) educational page at http://www.networkadvertising.org/understanding-online-advertising/how-does-it-work.&nbsp;</span><br />
<br />
<span>You can opt out of targeted advertising by using the links below:&nbsp;</span><br />
<span>- Facebook: https://www.facebook.com/settings/?tab=ads&nbsp;</span><br />
<span>- Google: https://www.google.com/settings/ads/anonymous&nbsp;</span><br />
<span>- Bing: https://advertise.bingads.microsoft.com/en-us/resources/policies/personalized-ads&nbsp;</span><br />
<br />
<span>Additionally, you can opt out of some of these services by visiting the Digital Advertising Alliance’s opt-out portal at: http://optout.aboutads.info/.&nbsp;</span><br />
<br />
<span style="font-weight:700;font-size:18px;">Do not track</span><span style="font-size:18px;">&nbsp;</span><br />
<span>Please note that we do not alter our Site’s data collection and use practices when we see a Do Not Track signal from your browser.&nbsp;</span><br />
<br />
<span style="font-weight:700;font-size:18px;">Your rights</span><span style="font-size:18px;">&nbsp;</span><br />
<span>If you are a European resident, you have the right to access personal information we hold about you and to ask that your personal information be corrected, updated, or deleted. If you would like to exercise this right, please contact us through the contact information below.&nbsp;</span><br />
<br />
<span>Additionally, if you are a European resident we note that we are processing your information in order to fulfill contracts we might have with you (for example if you make an order through the Site), or otherwise to pursue our legitimate business interests listed above. Additionally, please note that your information will be transferred outside of Europe, including to Canada and the United States.&nbsp;</span><br />
<br />
<span style="font-weight:700;font-size:18px;">Data retention</span><span style="font-size:18px;">&nbsp;</span><br />
<span>When you place an order through the Site, we will maintain your Order Information for our records unless and until you ask us to delete this information.&nbsp;</span><br />
<br />
<span style="font-weight:700;font-size:18px;">Changes</span><span style="font-size:18px;">&nbsp;</span><br />
<span>We may update this privacy policy from time to time in order to reflect, for example, changes to our practices or for other operational, legal or regulatory reasons.&nbsp;</span><br />
<br />
<span style="font-weight:700;font-size:18px;">Contact us</span><span style="font-size:18px;">&nbsp;</span><br />
<span>For more information about our privacy practices, if you have questions, or if you would like to make a complaint, please contact us by e‑mail at support@frissonlife.com or by mail using the details provided below:&nbsp;</span><br />
<br />
Frisson<br />
<span>[Re: Privacy Compliance Officer]&nbsp;</span><br />
<span>No. 19, 26 Chapter Street, London, SW1P 4NP, UK</span>
	<p>
		<br />
	</p>
</div>`;

